import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import WebApp from '@twa-dev/sdk';
import './App.css';
import SequencerPage from './pages/SequencerPage';
import SavedSessionsPage from './pages/SavedSessionsPage';

function App() {
  useEffect(() => {
    const initializeTelegramApp = async () => {
      try {
        if (WebApp.initData) {
          console.log('TMA environment detected via WebApp.initData. Initializing...');

          WebApp.ready();
          console.log('WebApp.ready() called.');

          if (WebApp.Viewport.isExpanded) {
            console.log('Viewport is already expanded.');
          } else {
            console.log('Expanding viewport...');
            WebApp.Viewport.expand();
          }

          console.log('To achieve fullscreen-like behavior, ensure viewport is expanded.');
          console.log('True fullscreen (hiding system UI) might need platform-specific handling or might not be available via this SDK.');

        } else {
          console.log('Not a TMA environment (WebApp.initData is missing).');
        }
      } catch (error) {
        console.error('Error initializing Telegram Mini App with @twa-dev/sdk:', error);
      }
    };

    initializeTelegramApp();

  }, []);
  return (
    <Router basename="/personal">
      <div className="App">
        <nav>
          <Link to="/">Главная</Link>
          <Link to="/sequencer">Секвенсор</Link>
          <Link to="/savedsessions">Сохраненные сессии</Link>
        </nav>

        <Routes>
          <Route path="/" element={
            <div className="cover-container">
              <img 
                src="cover.PNG" 
                alt="Обложка" 
                className="cover-image" 
              />
            </div>
          } />
          <Route path="/sequencer" element={<SequencerPage />} />
          <Route path="/savedsessions" element={<SavedSessionsPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;